.button {
    background-color: white;
    color: black;
    border: 2px solid #e7e7e7;
    border-radius: 3px;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;

    -webkit-transition-duration: 0.2s; /* Safari */
    transition-duration: 0.2s;
    cursor: pointer;

    &:hover {
        background-color: #e7e7e7d8;
    }
}

